import {
    UserRegister,
    UserLogin,
    UserProfile,
    BooksList,
    BookDetails,
    BookCreation,
    BookEdition,
  } from '../containers/pages';
  
  import App from '../containers/App';
  
  const routes = [
    {
      component: App,
      routes: [
        {
          component: UserRegister,
          path: '/'
        }
      ]
    }
  ];
  
  export default routes;
  


